import React from "react";

export const GiveThingsFormSent = () => {
  return (
    <div className="form-sent">
      <div className="form-sent-box">
        <h3 className="form-sent-box__title">
          Thank you for submitting the form. We will send you all the
          information about receipt.
        </h3>
        <img
          className="form-sent-box__decoration"
          alt="Decoration"
          src="/assets/Decoration.svg"
        />
      </div>
    </div>
  );
};
